import React, { Component } from 'react';
import {
    Col,
    Form,
    FormGroup,
    Input,
    Label,
    Row,
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    Button,
    Modal,
    ModalBody,
    ModalHeader,
    ModalFooter
} from "reactstrap";
import { BrowserRouter as Router, Route, Link, Redirect } from "react-router-dom";
import ModalRoom from '../../component/ModalRoom';


export default class RoomDetail extends Component {

    constructor(props) {
        super(props);
        this.state = {
            room: props.location.room,
            modal: false,
        }
        console.log(props.location.room);

    }

    toggleModal = () => {
        this.setState({ modal: !this.state.modal })
    }


    render() {
        return (
            <div>
                <Row>
                    <Col>
                        <Card>
                            <CardHeader>
                                <i className="fa fa-align-justify"></i> Detail Room Number {this.state.room.name}
                                <div className="card-header-actions">
                                    <Link to="/room"><Button className="card-header-action" color="primary" >Back</Button></Link>
                                    <Button className="card-header-action" color="primary" onClick={this.toggleModal}>Bidder</Button>
                                </div>
                            </CardHeader>
                            <CardBody>
                                <Form action="" method="post" encType="multipart/form-data" className="form-horizontal">
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="user-input">Auctioneer</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="user-input" name="user-input" value={this.state.room.user.username} disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="item-input">Item Name</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="item-input" name="item-input" value={this.state.room.item.title} disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="start-time-input">Start Time</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="start-time-input" name="start-time-input" value={this.state.room.startTime}
                                                disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="end-time-input">End Time</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="end-time-input" name="end-time-input" value={this.state.room.endTime} disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="address-input">Address</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="address-input" name="address-input" value={this.state.room.address} disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="status">Status</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" name="status" id="status" value={this.state.room.status} disabled />
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label>Type</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Input type="text" id="type-input" name="type-input" value={this.state.room.type} disabled />
                                        </Col>
                                    </FormGroup>
                                </Form>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>

                <Modal isOpen={this.state.modal} toggle={this.toggleModal} className={'modal-success ' + this.props.className}>
                    <ModalHeader toggle={this.toggleModal}><strong>{this.state.room.roomHistories.length} Bidder</strong></ModalHeader>
                    <ModalBody>
                        <ModalRoom histories={this.state.room.roomHistories} />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" onClick={this.toggleModal}>Close</Button>
                    </ModalFooter>
                </Modal>
            </div>
        )
    }
}
